:not(:defined) > * {
  display: none;
}

html {
  height: 100%;
}

body {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
}

model-viewer {
  background-color: #fff;
  width: 100%;
  height: 90%;
  box-shadow: 0 0 20px #00000020;
}

.progress-bar {
  background-color: #00000080;
  border: 1px solid #ffffffe6;
  border-radius: 25px;
  width: 33%;
  height: 10%;
  max-height: 2%;
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  box-shadow: 0 3px 10px 3px #00000080, 0 0 5px 1px #0009;
}

.progress-bar.hide {
  visibility: hidden;
  transition: visibility .3s;
}

.update-bar {
  float: left;
  background-color: #49fcd4;
  border-radius: 25px;
  width: 0%;
  height: 100%;
  transition: width .3s;
}

#ar-button {
  white-space: nowrap;
  color: #49fcd4;
  background-color: #fff;
  background-image: url("https://github.com/DanielMacura/DanielMacura.github.io/blob/c4dc83f44944478e75a02b4b95cc782caa4d56b8/images/modelviewer/ar_icon.png");
  background-position: 12px;
  background-repeat: no-repeat;
  background-size: 20px 20px;
  border: 1px solid #dadce0;
  border-radius: 18px;
  height: 36px;
  padding: 0 24px;
  font-family: Roboto Regular, Helvetica Neue, sans-serif;
  font-size: 14px;
  line-height: 36px;
  position: absolute;
  bottom: 16px;
  left: 50%;
  transform: translateX(-50%);
}

#ar-button:active {
  background-color: #49fcd4;
}

#ar-button:focus {
  outline: none;
}

#ar-button:focus-visible {
  outline: 1px solid #49fcd4;
}

@keyframes circle {
  from {
    transform: translateX(-50%)rotate(0)translateX(50px)rotate(0);
  }

  to {
    transform: translateX(-50%)rotate(360deg)translateX(50px)rotate(-360deg);
  }
}

@keyframes elongate {
  from {
    transform: translateX(100px);
  }

  to {
    transform: translateX(-100px);
  }
}

model-viewer > #ar-prompt {
  animation: 2s ease-in-out infinite alternate elongate;
  display: none;
  position: absolute;
  bottom: 60px;
  left: 50%;
}

model-viewer[ar-status="session-started"] > #ar-prompt {
  display: block;
}

model-viewer > #ar-prompt > img {
  animation: 4s linear infinite circle;
}

